import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';
import { Text, Localizer, withText } from 'preact-i18n';

import Error from '../error';
import Message from '../message';
import Install from '../install';

@observer
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showToken: false,
            isCapsLock: false,
            passwordType: 'password',
            email: this.props.email,
        };
    }

    handleEmailInput = async (e) => {
        const { userStore } = this.props.stores;
        const email = e.target.value;
        this.setState({ email });
        clearTimeout(this.checkEmailTimer);
        this.checkEmailTimer = setTimeout(() => userStore.checkEmail(email), 1000);
    }

    togglePassword = () => {
        let { passwordType } = this.state;
        if (passwordType === 'password') {
            passwordType = 'text';
        } else {
            passwordType = 'password';
        }
        this.setState({ passwordType });
    }

    updateCapsLockStatus = (isCapsLock) => {
        this.setState({
            isCapsLock,
        });
    }

    login = () => {
        const { email, password, token } = this.state;
        const { userStore } = this.props.stores;
        userStore.checkLogin({ email, password, token });
    }

    resetLoginMessages = () => {
        const { userStore } = this.props.stores;
        userStore.resetLoginMessages();
    }

    newUser = () => {
        const { email = '' } = this.state;
        route(`/register/${email}`);
    }

    forgotten = () => {
        const { email = '' } = this.state;
        route(`/forgotten/${email}`);
    }

    // pwaInstallClick = (e) => {
    //     // hide our user interface that shows our A2HS button
    //     // addBtn.style.display = 'none';

    //     // Show the prompt
    //     this.deferredPrompt.prompt();
    //     // Wait for the user to respond to the prompt
    //     this.deferredPrompt.userChoice.then((choiceResult) => {
    //         if (choiceResult.outcome === 'accepted') {
    //             console.log('User accepted the A2HS prompt');
    //         } else {
    //             console.log('User dismissed the A2HS prompt');
    //         }
    //         this.deferredPrompt = null;
    //     });
    // }

    // componentWillMount() {
    //     window.addEventListener('beforeinstallprompt', (e) => {
    //         // Prevent Chrome 67 and earlier from automatically showing the prompt
    //         e.preventDefault();
    //         // Stash the event so it can be triggered later.
    //         this.deferredPrompt = e;

    //         // // Update UI to notify the user they can add to home screen
    //         // addBtn.style.display = 'block';
    //     });
    // }

    changeLanguage = (e) => {
        const lang = e.target.value;
		const { appState } = this.props.stores;
        appState.setLanguage(lang);
    }

    validateLoginToken = async () => {
        const { userStore } = this.props.stores;
        const { loginToken } = this.props;
        // console.log(loginToken, util.isDefined(loginToken));
        const response = await userStore.validateLoginToken(loginToken);
        if (response && response.status === 200) {
            route(`/`);
        }
    }

    componentDidMount() {
        const { userStore } = this.props.stores;
        if (this.props.email) {
            clearTimeout(this.checkEmailTimer);
            this.checkEmailTimer = setTimeout(() => userStore.checkEmail(this.props.email), 1000);
        }
        const { loginToken } = this.props;
        if (util.isString(loginToken)) {
            this.validateLoginToken();
        }
    }

    render() {
        const { isCapsLock, email, password, token, passwordType } = this.state;
        const { appState, userStore } = this.props.stores;
        const { language = 'en' } = appState;

        const { emailIsAvailable, login, twoFactorLogin } = userStore;
        const { message, messageIcon, error, errorIcon } = login;
        const isValidEmail = util.validateEmail(email);
        const isOkToLogin = isValidEmail && password;

        const darkmode = true;

        return (
            <div class='container-fluid'>
                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 pt-5'>

                        {1 === 2 && util.isMobile() && !util.isStandalone() && (
                            <div class='mt-5 alert alert-success'>
                                <Text id='login.tip-1'>Appen kan installeres p친 telefonen din.</Text> <br />
                                <br />
                                <Text id='login.tip-2'>Bruk "Legg til p친 hjemskjerm"-funksjonen.</Text><br />
                                <br />
                                <Text id='login.tip-3'>Det er lurt 친 gj칮re dette f칮r du registrer deg.</Text>

                                {/* <button class='btn btn-primary' onClick={this.pwaInstallClick}>
                                    Legg til p친 hjemskjermen din
                                </button> */}
                            </div>
                        )}

                        <Install />

                        <div class='d-flex justify-content-center mb-5 mt-5'>
                            {darkmode ? <>
                                <img src={`https://themusher.app/assets/logo-qpaws-dark.png`} height='100' />
                            </> : <>
                                <img src={`https://themusher.app/assets/logo-qpaws-light.png`} height='100' />
                            </>}
                        </div>
                        {/* <div class='d-flex justify-content-center'>
                            <h5 class='mt-3 mb-5'><Text id='login.login'>Logg inn</Text></h5>
                        </div> */}

                        <Error message={error} icon={errorIcon} remove={this.resetLoginMessages} />
                        <Message message={message} icon={messageIcon} remove={this.resetLoginMessages}  />

                        <div class='form-group'>
                            {/* <label for='emailInput'><i class='far fa-envelope' /> <Text id='login.email'>E-post</Text></label> */}
                            <Localizer>
                                <input type='email'
                                    class={`form-control form-control-lg ${email ? (isValidEmail && !emailIsAvailable ? 'is-valid' : 'is-invalid') : ''}`}
                                    id='emailInput'
                                    onInput={this.handleEmailInput}
                                    value={email}
                                    onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                                    placeholder={<Text id='login.email'>Email</Text>}
                                />
                            </Localizer>
                            {email && isValidEmail && emailIsAvailable && <div class='invalid-feedback' style={`display: block;`}>
                                <Text id='login.email-not-found'>E-posten finnes ikke, men du kan registrere deg 游땎</Text>
                            </div>}
                            {email && !isValidEmail && <div class="invalid-feedback" style={'display: block;'}>
                                <Text id='login.email-not-valid'>Hmm, e-posten ser ikke helt riktig ut. Kan det v칝re en skrivefeil?</Text>
                            </div>}
                            {email && isValidEmail && !emailIsAvailable && <div class="valid-feedback" style={'display: block;'}>
                                <Text id='login.email-looks-good'>E-post ser bra ut.</Text>
                            </div>}
                        </div>

                        <div class='form-group'>
                            {/* <label for='passwordInput'><i class='fas fa-key' /> <Text id='login.password'>Passord</Text></label> */}
                            <div class='input-group'>
                                <Localizer>
                                    <input type={passwordType}
                                        class={`form-control form-control-lg`}
                                        id='passwordInput'
                                        onInput={linkState(this, 'password')}
                                        value={password}
                                        onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                                        placeholder={<Text id='login.password'>Password</Text>}
                                    />
                                </Localizer>
                                <div class='input-group-append'>
                                    <button class='btn btn-lg btn-primary' type='button'
                                        onClick={this.togglePassword}
                                    >
                                        <i class={`fas fa-eye${passwordType === 'text' ? '-slash' : ''}`} />
                                    </button>
                                </div>
                            </div>
                        </div>

                        {isCapsLock && <div class='alert alert-danger' role='alert'>
                            <Text id='login.caps-lock-on'>CAPS LOCK er p친!</Text>
                        </div>}

                        {twoFactorLogin && <>
                            <div class='form-group'>
                                {/* <label for='tokenInput'><i class='fas fa-unlock' /> <Text id='login.2fa-token'>2FA Token</Text></label> */}
                                <Localizer>
                                    <input type='number'
                                        class='form-control form-control-lg'
                                        id='tokenInput'
                                        onInput={linkState(this, 'token')}
                                        value={token}
                                        placeholder={<Text id='login.2fa-token'>2FA Token</Text>}
                                    />
                                </Localizer>
                            </div>
                        </>}

                        <div class='form-group'>
                            <div class="input-group mb-2">
                                <div class="input-group-prepend">
                                    <div class="input-group-text"><Text id='login.select-language'>Select language</Text></div>
                                </div>
                                <select
                                    class='custom-select custom-select-lg border-0 text-white bg-primary'
                                    onInput={this.changeLanguage}
                                >
                                    <option value='no' selected={language === 'no'}>游游 NO</option>
                                    <option value='en' selected={language === 'en'}>游섫릖 EN</option>
                                    <option value='es' selected={language === 'es'}>游쀯릖 ES</option>
                                    <option value='de' selected={language === 'de'}>游뾇릖 DE</option>
                                    <option value='fr' selected={language === 'fr'}>游游 FR</option>
                                    <option value='ru' selected={language === 'ru'}>游游 RU</option>
                                    <option value='se' selected={language === 'se'}>游젏릖 SE</option>
                                </select>
                            </div>
                        </div>

                        <button class={`btn btn-lg btn-block ${isOkToLogin ? 'btn-primary' : 'btn-primary'}`} type='button'
                            onClick={this.login}
                            disabled={!isOkToLogin}
                        >
                            <i class='fas fa-sign-in-alt mr-2' />
                            <Text id='login.login'>Logg inn</Text>
                        </button>

                        <small class='form-text text-muted mt-1 text-right'>
                            <button class='btn btn-small btn-link text-primary'
                                onClick={this.forgotten}
                            >
                                <Text id='login.forgot-password'>Glemt passordet?</Text>
                            </button>
                        </small>

                    </div>
                </div>

                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 mt-4'>
                    <div class='form-row align-items-center'>

                        <button class='btn btn-link btn-block text-white' type='button'
                            onClick={this.newUser}
                        >
                            <strong>
                                <Text id='login.no-account'>No account?</Text>
                            </strong><br />
                            <i class='fas fa-plus mr-2' />
                            <Text id='login.signup-free-account'>Please sign up for a free account here</Text>
                        </button>
                    </div>
                </div>

            </div>
        );
    }
}

export default Login;
